import React from 'react';

import Head from '../components/Head/Head';
import Layout from '../containers/Layout/Layout';
import Button from '../components/UI/Button/Button';

import classes from './404.module.scss';

const pageNotFound = () => {
  return (
    <Layout>
      <Head content="Page Not Found | 404 Page">Page Not Found</Head>
      <div className={classes.PageNotFound}>
        <div className={classes.Text}>
          S<span className={classes.blinkoften}>o</span>r
          <span className={classes.blinkless}>r</span>y, P
          <span className={classes.off}>a</span>g
          <span className={classes.off}>e</span>{' '}
          <span className={classes.blinkless}>N</span>
          <span className={classes.blinkoften}>o</span>t F
          <span className={classes.blinkoften}>o</span>
          <span className={classes.off}>u</span>n
          <span className={classes.blinkless}>d</span>!
        </div>
        <Button to="/">
          <span className={classes.ButtonText}>
            Quickly, let's head back home!{' '}
            <span className={classes.RightArrow}>&#8594;</span>
          </span>
        </Button>
      </div>
    </Layout>
  );
};

export default pageNotFound;
